export default {
    name: 'app-textarea',
    props: {
        value: {
            default: '',
        },
        placeholder: {
            default: '',
        },
        placeholderStyle: {
            type: [String, Array],
            default: function () {
                return '';
            }
        },
        placeholderClass: {
            type: Array,
            default: function () {
                return [];
            },
        },
        disable: {
            default: false,
        },
        maxlength: {
            default: 10000,
        },
        focus: {
            default: false,
        },
        confirmType: {
            default: 'done'
        },

        showBorder: {
            default: true,
        },
        borderColor: {
            default: '#cccccc',
        },
        borderRadius: {
            default: 8,
        },
        fontSize: {
            default: 32,
        },
        color: {
            default: '#555',
        },
        background: {
            default: '#fff',
        },
        paddingX: {
            default: 24,
        },
        paddingY: {
            default: 24,
        },
        defaultValue: {
            default: '',
        },
    },
    data() {
        return {
            showInput: !!this.focus,
            inValue: this.value ? this.value : this.defaultValue,
        };
    },
    methods: {
        handleInput(e) {
            this.inValue = e.detail.value;
        },
        complete(e) {
            this.showInput = false;
            this.$emit('input', this.inValue);
        },
    }
}
